/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 */

 .portfolio-grid {
	display: grid;
  	grid-template-columns: repeat(3, 1fr); /* Two columns */
  	gap: 20px; 
  }
  
  .portfolio-item {
	display: flex;
  	flex-direction: column;
  	border: 1px solid #ddd; /* Add border for a nice look */
  	padding: 20px;
	overflow: hidden; /* Hide overflowing content */
  }
  
  .portfolio-item h2 {
	font-size: 24px;
	margin-bottom: 10px;
	
  }
  .portfolio-item img {
	max-width: 100%;
	height: auto;
	border-radius: 5px;
	margin-bottom: 10px;
  }
  
  .portfolio-item p {
	margin-bottom: 0; /* Remove default margin for paragraphs */
  }